@import url('https://fonts.googleapis.com/css2?family=Protest+Revolution&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Protest+Guerrilla&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Protest+Strike&display=swap');

* {
    margin: 0;
    padding: 0;
}

.header {
    font-family: 'Protest Revolution', sans-serif;
    position: sticky;
    top: 0;
    left: 0;
    margin: 0;
    width: 100%;
    padding: 20px 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: rgba(4, 88, 99, 0.895);
}

.header .nav .a {
    text-align: right;
    color: rgb(255, 255, 255);
    text-decoration: none;
    margin: 19px;
    transition: all .5s ease-in-out;
    font-size: 21px;
}

.header .nav {
    text-align: right;
    display: inline-block;
    margin-right: 71px;
}

.header .nav .a:hover {
    color: rgb(156, 215, 241);
}

.header .img {
    text-align: left;
    margin: -19px;
    user-select: none;
    display: inline-block;
    margin-left: 32px;
    width: 100px;
}

.header .h2 {
    margin: 0;
    padding: 0;
    color: white;
    letter-spacing: 2.6px;
    font-size: 43px;
    font-family: 'Protest Guerrilla', sans-serif;
}

.left {
    display: flex;
    margin-left: 20px;
}

.wq {
    display: flex;
}

::-webkit-scrollbar {
    display: none;
}