.wapper {
    position: relative;
    width: 495px;
    height: 527px;
    background: transparent;
    border: 2px solid rgba(255, 255, 255, 0.416);
    border-radius: 20px;
    backdrop-filter: blur(20px);
    display: flex;
    justify-content: center;
    color: aliceblue;
    align-items: center;
}

.bodysignup {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: url("../Images/BG.jpg") no-repeat;
    background-size: cover;
    background-position: center;
}

.form-login {
    width: 100%;
    padding: 40px;
}

.form-login h2 {
    font-size: 2em;
    text-align: center;
}

.input-box {
    position: relative;
    width: 100%;
    height: 50px;
    margin: 30px 0;
    outline: none;
}

.input-box input {
    width: 70%;
    border: none;
    border-bottom: 2px solid rgb(255, 255, 255);
    color: white;
    background: none;
    transition: all .5s ease-in-out;
    outline: none;
}

.input-box input:hover,
.input-box input:focus {
    width: 90%;
}

.forgot {
    text-align: right;
}

.forgot a {
    color: rgb(255, 255, 255);
    text-decoration: none;
}

.buttion {
    text-align: center;
    margin: 20px 0;
}

.buttion button {
    width: 100%;
    background-color: #2196F3;
    border: none;
    color: white;
    padding: 8px 18px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 18px;
    border-radius: 12px;
}

.buttion button:hover {
    background-color: #008cff;
    cursor: pointer;
}

.register {
    text-align: center;
}

.register a {
    color: rgb(255, 255, 255);
    text-decoration: none;
}

.input-box img {
    display: flex;
    float: right;
    filter: brightness(0) invert(1);
}