@import url('https://fonts.googleapis.com/css2?family=Protest+Revolution&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Protest+Guerrilla&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Protest+Strike&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Workbench&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Titan+One&display=swap');

.main {
    height: 573px;
    width: 100%;
    background: rgb(31, 96, 116);
    background: linear-gradient(180deg, rgba(31, 96, 116, 1) 0%, rgba(2, 172, 150, 1) 100%);
}

.main .slogan {
    width: 90%;
    padding: 105px;
    padding-top: 200px;
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
}

.main .slogan p {
    color: aliceblue;
    font-family: 'Protest Strike', sans-serif;
    font-size: 87px;
}

.about-us {
    padding: 30px;
    background-color: beige;
}

.about-container {
    margin: 55px;
    display: flex;
}

.aboutus-Topic {
    font-family: 'Workbench', sans-serif;
    font-size: 33px;
    font-weight: bold;
    border-bottom: solid green;
    width: 100%;
    letter-spacing: 4px;
}

.about-Para {
    font-family: 'Lobster', sans-serif;
    font-size: 22px;
    margin-top: 21px;
    letter-spacing: 0.8px;
    line-height: 1.25;
    width: 70%;
}

.about-img {
    height: 242px;
}

.Made {
    font-family: 'Titan One', sans-serif;
    font-size: 21px;
    margin-top: 21px;
    letter-spacing: 1px;
    line-height: 1.5;
    font-weight: bolder;
    margin-left: 10px;
}

#svg-1 {
    animation: svg1 5s cubic-bezier(1, .46, 0, 1.59) infinite;
}

.svg {
    width: 1531px;
}

@keyframes svg1 {
    from {
        stroke-dasharray: 1000;
        stroke-dashoffset: 1000;
    }

    to {
        stroke-dashoffset: 0;
    }
}

.contactus {
    width: 100vw;
    background-color: black;
    display: flex;
    flex-direction: row;
}

.h2 {
    color: white;
}

.git {
    color: white;
}

.number {
    color: white;
}

.stpanel {
    width: 100%;
}

.panel {
    width: 100%
}

.container-contactus {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
    font-family: 'Poppins', sans-serif;
}

.input,
.textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
    resize: vertical;
}

.submit {
    background-color: #04AA6D;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.submit:hover {
    background-color: #45a049;
}

:root {
    --scroll: 0;
}