.parent {
    height: 468px;
    width: 1477px;
    border-radius: 12px;
    border: 2px solid;
    margin: 87px;
    margin-top: 158px;
}

.parentheader {
    display: flex;
    padding: 2px;
}

.elements {
    height: 39px;
    border: 2px solid;
    margin: 0.5px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Poppins', sans-serif;
}

.element {
    height: 39px;
    border: none;
    border-bottom: 2px solid gray;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Poppins', sans-serif;
}

.eachdrive {
    display: flex;
}

.SNO {
    width: 66px;
}

.name {
    width: 209px;
}

.city {
    width: 150px
}

.startdate {
    width: 142px;
}

.enddate {
    width: 142px;
}

.description {
    width: 353px;
}

.register {
    width: 265px;
}

.eachdrive .SNO {
    width: 84px;
}

.body-4 {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-size: cover;
    background-position: center;
    flex-direction: column;
}