.wrapper {
    position: relative;
    margin-top: 80px;
    margin-left: 107px;
    width: 1059px;
    height: 527px;
    background: rgb(156, 215, 241);
    border-radius: 5% 4% 5% 7% / 45% 10% 10% 55%;
    display: flex;
    justify-content: center;
    color: rgb(255, 255, 255);
    align-items: center;
    font-family: 'Poppins', sans-serif;

}

.Register {
    font-size: 32px;
    text-align: center;

}

.panel {
    margin: 40px;
    background: white;
    height: 100px;
    margin: 40px;
    width: 100%;
}

.rightpanel {
    margin-right: 62.5px;
}

.leftpanel {
    margin-left: 62.5px;
}

.table {
    display: flex;
    width: 100%;
}

.input-box {
    margin: 40px;
    width: 350px;
}

.input-box label {
    font-size: 20px;
}

.input-box input {
    height: 32px;
    width: 90%;
    border: none;
    background-color: transparent;
    border-bottom: 2px solid rgb(255, 255, 255);

}

.input-box input:hover,
.input-box input:focus {
    width: 90%;
}

.button {
    width: 90%;
    margin-left: 50px;
    border-radius: 50px;
    height: 30px;
    font-size: 20px;
    background-color: rgb(117, 200, 252);
    border: none;
}

