.parent {
    height: 499px;
    width: 359px;
    border: 2px solid;
    margin-top: 91px;
    border-radius: 13px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.parent .img {
    aspect-ratio: 1/1;
    height: 120px;
    border-radius: 50px;
    border: 2px solid;
    margin-top: 16px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.parent .img img {
    height: 122px;
    aspect-ratio: 1/1;
}

.text {
    height: 287px;
    margin: 26px;
    padding: 12px;
    width: 90%;
}

.content {
    margin: 13px;
    font-size: 21px;
    font-family: 'Poppins', sans-serif;

}

.body-profile {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-size: cover;
    background-position: center;
}